import React, { useEffect } from "react";
import Container from "../Components/Container";
import Header from "../Components/Header";
import Contents from "../Components/Contents";
import Card from "../Components/Card";
import All_Array from "../Assets/Data/All_Array";
import { Helmet } from "react-helmet";
import ListHeader from "../Components/ListHeader";

const Detail = ({ history }) => {
  const pathOriginal = history.location.pathname;
  const pathEdited = pathOriginal.substr(1, pathOriginal.length).toLowerCase();
  console.log(All_Array[pathEdited]);
  const [headerArray, dataArray] = All_Array[pathEdited];
  const contentsArray = dataArray(headerArray.themeColor);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <Container>
      <Helmet>
        <title>{headerArray.title}</title>
      </Helmet>
      <Header
        history={history}
        stage={headerArray.stage}
        title={headerArray.title}
        desc={headerArray.desc}
        channelArray={headerArray.channelArray}
        author={headerArray.author}
        authorUrl={headerArray.authorUrl}
      ></Header>
      <Contents
        stage={headerArray.stage}
        history={history}
        main={headerArray.main}
      >
        <ListHeader
          listTitle={headerArray.stage === "Detail" ? "About Montage" : "Steps"}
        ></ListHeader>
        {contentsArray.map((data, index) => (
          <Card
            key={index}
            stage={headerArray.stage}
            textColor="white"
            leftText={`#${index + 1}`}
            title={data.title}
            subTitle={data.subTitle}
            desc={data.desc}
            img={data.img}
            imgType={data.imgType ? data.imgType : null}
            category={`${index + 1}/${contentsArray.length}`}
          ></Card>
        ))}
      </Contents>
    </Container>
  );
};

export default Detail;
